@import url('https://fonts.googleapis.com/css2?family=Rubik:wght@300;400&display=swap');
body {
  height: 400vh;
}
* {color: aliceblue; font-family: 'Rubik';}

.title {
  font-size: large;
}

.page {
  display: grid;
  grid-template-columns: auto auto auto;
  flex-direction: row;
  margin: 5px;
  padding: 5px;
  align-items: flex-start;
}

.Day {
  width: auto;
  height: auto;
  background-color: #161925;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  margin: 5px;
  padding: 10px;
  transition: ease 1s;
}

@media (max-width: 800px) {
  .page {
    flex-direction: column;
    grid-template-columns: auto;
  }
}

.titleDiv, .unavailable {
  height: auto;
  width: auto;
  background-color: #161925;
  font-weight: 400;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.titleDiv, .unavailable {
  padding: 10px;
  border-radius: 30px;
  margin: 10px 10px 0px 10px;
}

.titleDiv p {
  font-size: 20px;
  margin: 1px;
  text-align: center;
}

.unavailable {
  font-size: 30px;
}

.hide {
  border-color: transparent;
  border: none;
  background-color: transparent;
}